.Container{
    padding: 4vw;
    align-items: center;
}
.heading{
    color: white;
    text-align: center;
}

.textrow{
    text-align: center;
    display: block;
    padding:8px;
   margin-left: 5vw;
    margin: auto;
    font-weight: bold;
    color: white;
}

table {
    font-family: Arial, sans-serif;
    border-collapse: collapse;
    width: 100%;
    margin: 20px 0;
}


th, td {
    color: black;
    text-align: center;
    padding: 8px;
    border-bottom: 1px solid #ddd;
    height: 2vw;
}

th {
    background-color: #04AA6D;
    color: white;
}

tr:hover {
    background-color: #f5f5f5;
}

td:nth-child(1) {
    background-color: #f2f2f2;
    width: 30%;
}

td:nth-child(2) {
    background-color: #f2f2f2;
    width: 20%;
}

td:nth-child(3) {
    background-color: #f2f2f2;
    width: 25%;
}

td:nth-child(4) {
    background-color: #f2f2f2;
    width: 15%;
}

td:nth-child(5) {
    background-color: #f2f2f2;
    width: 10%;
}