
.maincontainer{
    display: flex;
    flex: 50% 50%;
    align-items: center;
}

.flex-col{
    display: flex;
    flex-direction: column;
}

.rightcontainer{
    margin: 3vw;
    padding: 20px;
    color: #fff;
}
.tc{
    text-align: center;
}


.fs-1{
    font-size: 22px;
    margin-bottom: 5px;
}
.mb-1{
    margin-top: 15px;
}

.lefcontainer img{
    width: 30vw;
}
.btn{
    padding: 1vw 7vw;
    font-size: 18px;
    margin-top: 9px;
    cursor: pointer;
}
.btn:hover{
    background-color: rgb(156, 253, 250);
    scale: 1.1;
}



/* sytle for Radio Button  */
.form-radio{
    height: 18px;
    width: 18px;
    
}
.tc h1{
    font-family: cursive;
    font-size: 2.5vw;
    color: rgb(229, 231, 231);
}
.fs-2{
    font-size: 18px;
    padding: 10px;
}

/* Responsiveness */
@media screen and (max-width: 960px) {
    .maincontainer{
        display: flex;
        flex-direction: column-reverse;
    }
}