.navbar {
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 1.2rem;
  }
  
  .navbar-toggle {
    display: none;
    cursor: pointer;
    padding: 10px;
    z-index: 100;
  }
  
  .bar {
    width: 30px;
    height: 5px;
    background-color:white;
    margin: 10px 10px;
    transition: all 0.3s ease-in-out;
  }
  
  .navbar-nav {
    display: flex;
    justify-content: center;
    align-items: center;
    align-items: center;
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  .nav-item {
    height: 80px;
  }
  
  .nav-link {
    color: #fff;
    text-decoration: none;
    padding: 1rem 2rem;
    display: flex;
    align-items: center;
    transition: all 0.3s ease-out;
  }
  
  .nav-link:hover {
    transform: translateY(-2px);
  }
  
  @media screen and (max-width: 960px) {
    .navbar-nav {
      flex-direction: column;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
      width: 100%;
      background-color: #242424;
    }
  
    .navbar-nav.active {
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
    }
  
    .navbar-toggle {
      display: block;
    }
  
    .nav-item {
      height: 60px;
      border-bottom: 1px solid #fff;
      width: 100%;
      display: flex;
      justify-content: center;
    }
  
    .nav-link {
      text-align: center;
      width: 100%;
      display: table;
    }
  }
  